* {
  margin: 0;
  font-size: 14pt;
}
#root {
  height: 100vh;
}
.green-background {
  background-color: #00ba68;
}
.align-center {
  text-align: center;
}
.gray-background {
  background-color: #eff2f5;
}
.main-title {
  height: 100px;
  padding-top: 40px;
  color: #FFFFFF;
  font-size: 3rem;
}
.section-title {
  height: 80px;
  background-color: #eff2f5;
  color: #5fd09b;
}
.section-title > h2 {
  text-align: center;
  padding: 20px;
  font-size: 2rem;
}
.bank-card-container,
.branch-card-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.bank-card,
.branch-card {
  width: 250px;
  border: solid black;
  height: 250px;
  margin: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.bank-card > h1,
.branch-card > h1 {
  margin: 5px 0px 5px 0px;
  font-size: 2rem;
}
.bank-card > p,
.branch-card > p {
  margin: 5px 0px 5px 0px;
}
.form-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.form-container > h1,
.form-container > form,
.form-container > button{
  margin: 10px auto;
}
.form-input {
  margin: 10px;
  border-radius: 5px;
}